define('dummy/mirage/config', ['exports'], function (exports) {
  exports['default'] = function () {
    this.get('/dummy-records', function (db) {
      return {
        data: db['dummy-records'].map(function (attrs) {
          return {
            type: 'dummy-records',
            id: attrs.id,
            attributes: attrs
          };
        })
      };
    });

    this.passthrough();
  };
});